import Slide from "../components/Slide";

const getCarousel = (elem, variable, arrItems, db , ...arr) =>{
    variable == 0 ?
    variable = 2:
    variable -= 1;   

    Slide(elem, 
        db[variable].image,
        db[variable].name,
        db[variable].description,
        db[variable].price,
        ...arr);

    arrItems.forEach((item,index) => {
        index == variable ?
        item.firstElementChild.classList.add('anime'):
        item.firstElementChild.classList.remove('anime');
       
    })
}

export default getCarousel;