import './../styles/style.scss';
import {animeSpaceToCenter,animeSpaceToEnd,animeSpaceFromEnd,
    animeSpaceFromCenter,toOpacity,toNonOpacity,animeSetting} from './../js/animation';

const Slide = (elem,pic, name, description, price, anSpace, anOpacity) =>{
    elem.removeChild(elem.firstChild);
    
    const slide = document.createElement('div');
    slide.classList.add('slide');
    slide.innerHTML = `
    <img src=${pic} alt="${name}">
    <h3>${name}</h3>
    <p>${description}</p>
    <span class="favorite-slide-price">${price}</span>
    `;
    elem.prepend(slide);
    slide.animate(anSpace,animeSetting);
    slide.animate(anOpacity,animeSetting);
}

export default Slide;