import './../src/styles/style.scss';
import './../src/styles/vars.scss';
import './../src/styles/header_home.scss';
import './../src/styles/tab.scss';

import Header from "./components/Header";
import Enjoy from './components/Enjoy';
import Favorite_Coffe from './components/Favorite_coffe';
import About from './components/About';
import App from './components/App';
import Footer from './components/Footer';
import fav from './components/favi';

import {animeSpaceToCenter,animeSpaceToEnd,animeSpaceFromEnd,
    animeSpaceFromCenter,toOpacity,toNonOpacity,animeSetting} from './js/animation';
import {showMenu, hideMenu} from './js/changeBurger';
import Slide from './components/Slide';
import getCarousel from './js/getCarousel';
import slideInfo from'./json/slider.json';
import Video from './assets/images/video.mp4';
import Frappuchino from './assets/images/frappuchino.png';
import TeaWithIce from './assets/images/tea-with-ice.png';
import Latte from './assets/images/latte.png';
import favi from './assets/icons/coffee-cup.png'
const head = document.querySelector('head');
const container = document.querySelector('.container');
const main = document.createElement('main');

fav(head,favi);
Header(container);
container.append(main);
Enjoy(main);
Favorite_Coffe(main);
About(main);
App(main);
Footer(container);

const burger = document.querySelector('.menu-burger');
const spans = burger.querySelectorAll('span');
const menu = document.querySelector('.menu');
const itemSlide = document.querySelector('.favorite-slide');
const leftButtonSlide =document.querySelector('.left-btn');
const rightButtonSlide =document.querySelector('.right-btn');
const navs = document.querySelectorAll('.favorite-nav');
window.onload = ()=>{
    nowInterval();
}


    burger.addEventListener('click',(event)=>{
        event.target.classList.contains('burger-active')||
        event.target.classList.contains('span-active')?
            hideMenu(burger,spans, menu):
            showMenu(burger,spans, menu);   
        
    } );
    
    menu.addEventListener('click', (event) => {
        if(event.target.classList.contains('menu-link')||
        event.target.classList.contains('menu-list-link')){
         event.preventDefault();
         hideMenu(burger,spans, menu);
         setTimeout(()=>{
             window.location =event.target.href
         }, 500);   
    }
    });
    
    // slider
    
   
    
    let number = 1;
    getCarousel(itemSlide, number, navs, slideInfo);
    
    number = 2;
    let interval;
    let pauseInterval = false;
    const nowInterval= function (){
        clearInterval(interval);
      interval = setInterval(()=>{
        if(!pauseInterval){
            number == 0 ?
            number = 2:
            number -= 1;

            itemSlide.firstChild.animate(animeSpaceFromCenter,animeSetting);
            itemSlide.firstChild.animate(toNonOpacity,animeSetting);
                
            setTimeout(Slide, 950 , 
                itemSlide, 
                slideInfo[`${number}`].image,
                slideInfo[`${number}`].name,
                slideInfo[`${number}`].description,
                slideInfo[`${number}`].price,
                animeSpaceFromEnd, toOpacity);
        
                navs.forEach((nav,index) => {
                index == number?
                nav.firstElementChild.classList.add('anime'):
                nav.firstElementChild.classList.remove('anime');
               
            })
        }

          
            
        },7000);
    }
    
    // nowInterval(); 
    
    leftButtonSlide.addEventListener('click', ()=>{
        number == 0 ?
        number = 2:
        number -= 1;
        itemSlide.firstChild.animate(animeSpaceFromCenter,animeSetting);
        itemSlide.firstChild.animate(toNonOpacity,animeSetting);
            
        setTimeout(Slide, 950 , 
            itemSlide, 
            slideInfo[`${number}`].image,
            slideInfo[`${number}`].name,
            slideInfo[`${number}`].description,
            slideInfo[`${number}`].price,
            animeSpaceFromEnd, toOpacity);
    
            navs.forEach((nav,index) => {
            index == number?
            nav.firstElementChild.classList.add('anime'):
            nav.firstElementChild.classList.remove('anime');
           
        })
     clearInterval(interval);
     nowInterval();
    })
    
    rightButtonSlide.addEventListener('click', ()=>{
        number == 2 ?
        number = 0:
        number += 1;
        itemSlide.firstChild.animate(animeSpaceToEnd,animeSetting);
        itemSlide.firstChild.animate(toNonOpacity,animeSetting);
            
        setTimeout(Slide, 950 , 
            itemSlide, 
            slideInfo[`${number}`].image,
            slideInfo[`${number}`].name,
            slideInfo[`${number}`].description,
            slideInfo[`${number}`].price,
            animeSpaceToCenter, toOpacity);
    
            navs.forEach((nav,index) => {
            index == number?
            nav.firstElementChild.classList.add('anime'):
            nav.firstElementChild.classList.remove('anime');
           
        })
        clearInterval(interval);
        nowInterval();
    })
    
    navs.forEach((nav, index) => {
        nav.addEventListener('click',(event)=>{
            if(event.target == nav){
                number = index;
                itemSlide.firstChild.animate(animeSpaceFromCenter,animeSetting);
                itemSlide.firstChild.animate(toNonOpacity,animeSetting);
                    
                setTimeout(Slide, 1950 , 
                    itemSlide, 
                    slideInfo[`${number}`].image,
                    slideInfo[`${number}`].name,
                    slideInfo[`${number}`].description,
                    slideInfo[`${number}`].price,
                    animeSpaceFromEnd, toOpacity);
            
                    navs.forEach((nav,index) => {
                    index == number?
                    nav.firstElementChild.classList.add('anime'):
                    nav.firstElementChild.classList.remove('anime');
                   
                })
             clearInterval(interval);
             nowInterval();
            }
        })
    })
    
   
        itemSlide.addEventListener('mouseover',()=>{
            pauseInterval = true;
            console.log('true')
            // clearInterval(interval);
            console.log( navs[number])
            navs[number].firstChild.classList.add("pause-anime");
        })
        itemSlide.addEventListener('mouseout',()=>{
            pauseInterval = false;
            // nowInterval();
            console.log('false')
           console.log( navs[number])
           navs[number].firstChild.classList.remove("pause-anime");
        })
      
    
    console.log(itemSlide.children)


    let touchPoint;
    itemSlide.addEventListener('touchstart',(event) =>{
        touchPoint = event.changedTouches[0].clientX;})
    itemSlide.addEventListener('touchend',(event) =>{
       if( event.changedTouches[0].clientX > touchPoint){
            number == 0 ?
            number = 2:
            number -= 1;
            itemSlide.firstChild.animate(animeSpaceFromCenter,animeSetting);
            itemSlide.firstChild.animate(toNonOpacity,animeSetting);
                
            setTimeout(Slide, 950 , 
                itemSlide, 
                slideInfo[`${number}`].image,
                slideInfo[`${number}`].name,
                slideInfo[`${number}`].description,
                slideInfo[`${number}`].price,
                animeSpaceFromEnd, toOpacity);
        
                navs.forEach((nav,index) => {
                index == number?
                nav.firstElementChild.classList.add('anime'):
                nav.firstElementChild.classList.remove('anime');
               
            })
         clearInterval(interval);
         nowInterval();
        }
        if(event.changedTouches[0].clientX < touchPoint){
            number == 2 ?
            number = 0:
            number += 1;
            itemSlide.firstChild.animate(animeSpaceToEnd,animeSetting);
            itemSlide.firstChild.animate(toNonOpacity,animeSetting);
                
            setTimeout(Slide, 950 , 
                itemSlide, 
                slideInfo[`${number}`].image,
                slideInfo[`${number}`].name,
                slideInfo[`${number}`].description,
                slideInfo[`${number}`].price,
                animeSpaceToCenter, toOpacity);
        
                navs.forEach((nav,index) => {
                index == number?
                nav.firstElementChild.classList.add('anime'):
                nav.firstElementChild.classList.remove('anime');
               
            })
            clearInterval(interval);
            nowInterval();
        }
    })













